.shopping-cart {
  margin: 20px 0;
  float: left;
  background: white;
  width: 320px;
  position: relative;
  border-radius: 3px;
  padding: 20px; }
  .shopping-cart .shopping-cart-header {
    border-bottom: 1px solid #E8E8E8;
    padding-bottom: 15px; }
    .shopping-cart .shopping-cart-header .shopping-cart-total {
      float: right; }
  .shopping-cart .shopping-cart-items {
    padding-top: 20px; }
    .shopping-cart .shopping-cart-items li {
      margin-bottom: 18px; }
    .shopping-cart .shopping-cart-items img {
      float: left;
      margin-right: 12px; }
    .shopping-cart .shopping-cart-items .item-name {
      display: block;
      padding-top: 10px;
      font-size: 16px; }
    .shopping-cart .shopping-cart-items .item-price {
      color: #6394F8;
      margin-right: 8px; }
    .shopping-cart .shopping-cart-items .item-quantity {
      color: #ABB0BE; }

.shopping-cart:after {
  bottom: 100%;
  left: 89%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-bottom-color: white;
  border-width: 8px;
  margin-left: -8px; }

